.container-custom {
  width: 100%;
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.section {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

@media (max-width: 768px) {
  .section {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}

.btn {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  font-weight: 600;
  border-radius: 0.375rem;
  transition: all 0.3s ease;
  text-align: center;
}

.btn-primary {
  background-color: #3b82f6;
  color: white;
}

.btn-primary:hover {
  background-color: #2563eb;
}

.btn-secondary {
  background-color: #e5e7eb;
  color: #1f2937;
}

.btn-secondary:hover {
  background-color: #d1d5db;
}

.bg-dark-500 {
  background-color: #111827;
}

.text-primary-500 {
  color: #3b82f6;
}

.text-primary-600 {
  color: #2563eb;
}

.text-primary-800 {
  color: #1e40af;
}

.bg-primary-100 {
  background-color: #dbeafe;
}

.bg-primary-500 {
  background-color: #3b82f6;
}

.bg-primary-600 {
  background-color: #2563eb;
}

.border-primary-500 {
  border-color: #3b82f6;
}

.text-secondary-500 {
  color: #10b981;
}

.bg-secondary-400 {
  background-color: #34d399;
}

.bg-secondary-500 {
  background-color: #10b981;
}

.from-primary-400 {
  --tw-gradient-from: #60a5fa;
}

.to-secondary-400 {
  --tw-gradient-to: #34d399;
}

.from-secondary-400 {
  --tw-gradient-from: #34d399;
}

.to-primary-400 {
  --tw-gradient-to: #60a5fa;
}

/* アニメーション */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.animate-fadeIn {
  animation: fadeIn 1s ease-in-out;
}

/* アクセシビリティ向上 */
:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

/* レスポンシブ調整 */
.aspect-w-16 {
  position: relative;
  padding-bottom: calc(var(--tw-aspect-h) / var(--tw-aspect-w) * 100%);
  --tw-aspect-w: 16;
}

.aspect-h-9 {
  --tw-aspect-h: 9;
}

.aspect-w-16 > * {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
